* {
	font-family: 'Poppins', sans-serif;
	color: white;
}
.App {
	width: 100%;
	height: 100%;

	.weather-container {
		background-image: url('../img/944291.jpg');
		background-size: contain;
		background-repeat: no-repeat;
		min-width: 100%;
		min-height: 100vh;
	}
	.switch-options--btn {
		width: 36px !important;
		height: 36px !important;
		background-color: transparent;
		border: unset;
	}
	.switch-options--btn-active {
		background-color: rgba($color: #999, $alpha: 0.5);
		border: none;
	}
	.switch-options--btn:hover {
		background-color: rgba($color: #999, $alpha: 0.34);
	}
	.daily-card-temp--active {
		background-color: rgba(255, 255, 255, $alpha: 0.25);
	}
	.temp {
		color: #444;
	}
	.daily-list {
		// overflow: hidden;
		overflow-x: auto;
	}
	.daily-list::-webkit-scrollbar {
		display: none;
	}
	.pointer {
		height: max-content;
		position: relative;
		cursor: pointer;
		// padding: 4px;
		// background-color: ;
		border: 4px solid rgba(0, 0, 0, 0);
		width: 120px;
	}
	
	.pointer:hover {
		background-color: rgba(255, 255, 255, 0.45);
		// background-color: white;
		border: 4px solid white;
	}
}

.arrow-down {
	
	// width: 0;
	// height: 0;
	border-left: 20px solid transparent;
	border-right: 20px solid transparent;
	border-top: 20px solid  rgba(255, 255, 255, 0.45);
	position: absolute;
	top: calc(100% + 4px);
	left: 50%;
	transform: translateX(-50%);
	
}
